.PhotoGallery {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.MainContainer#PhotoGalleryMainContainer {
  width: 100%;
  height: 100%;
  margin-bottom: 2%;
}

.Container.PhotoGallery.PhotoGalleryItems {
  width: 100%;
  height: 100%;
}

.Container.PhotoGallery.PhotoGalleryItem.ProposalBox {
  width: 95%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2%;
  color: black;
  border: #c29e29 2px solid;
  background-color: white;
}

.Container.PhotoGallery.PhotoGalleryItem.ProposalBox > h1 {
  font-family: "Abhaya Libre";
  color: black;
  font-weight: 600;
  font-size: 300%;
  margin-top: 4%;
}

.Container.PhotoGallery.ProposalImages {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 3%;
  margin-top: 2%;
}

.PhotoGallery.ProposalImageBox {
  margin-bottom: 1%;
  transition: scale 1s;
}

.PhotoGallery.ProposalImage {
  max-height: 49vh;
  max-width: 42vw;
  object-fit: cover;
  transition: scale 1s;
  z-index: 0;
}

@media screen and (max-width: 1080px) {
  .MainContainer#PhotoGalleryMainContainer {
    width: 90%;
    padding-top: 8%;
  }
  .Container.PhotoGallery.PhotoGalleryItem.ProposalBox {
    width: 100%;
    min-height: fit-content;
  }
  .Container.PhotoGallery.PhotoGalleryItem.ProposalBox > h1 {
    font-family: "Abhaya Libre";
    color: black;
    font-weight: 600;
    font-size: 300%;
    margin-top: 4%;
    margin-bottom: 5%;
  }
  .Container.PhotoGallery.ProposalImages {
    display: flex;
    flex-direction: column;
  }
  .PhotoGallery.ProposalImageBox {
    padding-bottom: 10px;
    position: relative;
    outline: 5px solid black;
    background-color: black;
    width: 90%;
    height: fit-content;
  }
  .PhotoGallery.ProposalImage {
    max-height: fit-content;
    max-width: 90%;
    object-fit: scale-down;
    transition: scale 1s;
    z-index: 0;
  }
  .PhotoGallery.ProposalImage:hover {
    scale: 102%;
  }
}
@media screen and (min-width: 1080px) {
  .Container.PhotoGallery.PhotoGalleryItem.ProposalBox {
    width: 90% !important;
  }
}

