.leaflet-container {
  height: 50vh;
  width: 60%;
  z-index: 1;
}

#TravelMapMainContainer {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 5%;
  z-index: 0;
  margin-top: -2%;
}

.MapLockContainer {
  width: 60%;
  height: 50vh;
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.MapLock {
  position: absolute;
  left: 10px;
  top: 72px;
  width: 34px;
  height: 34px;
  z-index: 10;
  background-color: white;
  border-left: 2px solid rgba(102, 102, 102, 0.411);
  border-right: 2px solid rgba(102, 102, 102, 0.411);
  border-bottom: 2px solid rgba(102, 102, 102, 0.411);
  border-top: 1px solid rgba(102, 102, 102, 0.411);
  border-radius: 3px;
  padding-left: 12px;
  padding-right: 11px;
  padding-top: 2px;
  padding-bottom: 4px;
}

.MapLock img {
  width: 24px;
  top: 3px;
  left: 3px;
  position: absolute;
}

.MapLock:hover {
  background-color: rgb(243, 243, 243);
}

.leaflet-popup {
  max-width: 20vw;
  min-width: 250px;
}

div.leaflet-popup-content {
  margin: 0 !important;
  min-width: 200px;
  max-width: 100% !important;
}

.leaflet-popup-content-wrapper {
  padding: 0 !important;
  min-width: 200px;
  max-width: 100% !important;
}

.LocationContainer {
  display: flex;
  position: relative;
  flex-direction: row;
  justify-content: flex-start;
  min-width: 200px;
  width: 100%;
  height: fit-content;
}

.LocationImageContainer {
  overflow: hidden;
  width: 100%;
  max-height: 18vh;
  height: 45%;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.LocationImage {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 0% 70%;
}

.LocationInfoContainer {
  display: flex;
  width: 100%;
  height: 120px;
  flex-direction: column;
  justify-content: center;
  position: relative;
}

.LocationRatingContainer {
  bottom: 15px;
  display: flex;
  flex-direction: row;
  max-width: 100%;
  position: absolute;
  left: 10px;
  justify-content: flex-start;
}

.LocationRatingNum {
  font-size: small;
}

.LocationHeroContainer {
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: center;
  position: absolute;
  bottom: 0;
  left: 10px;
  justify-content: flex-start;
}

.LocationType {
  font-size: smaller;
}

.LocationHeroItem {
  width: 7%;
  height: 30%;
  margin-left: 5%;
}

.LocationButton {
  position: absolute;
  right: 5%;
  bottom: 5%;
  border: 1px solid black;
  border-radius: 15px;
  padding: 2%;
  cursor: pointer;
  transition: all 0.25s;
  color: black !important;
}

.LocationButton:hover {
  background-color: #c29e29;
  color: white !important;
}

.LocationName {
  z-index: 100 !important;
  color: black;
  width: 100%;
  font-weight: 540;
  font-size: medium;
  position: absolute;
  top: 0;
  left: 10px;
}

@media screen and (max-width: 768px) {
  .leaflet-container {
    width: 90%;
  }
  .leaflet-popup {
    max-width: 50vw;
    min-width: 50vw;
  }
  .LocationImageContainer {
    display: none;
  }
  .LocationContainer {
    max-height: fit-content;
  }
  .LocationName {
    text-align: center;
    max-width: 100%;
    left: 0;
  }
  .LocationButton {
    right: 5%;
    bottom: 3%;
    text-align: center;
    width: 35%;
  }
  .MapLockContainer {
    width: 90%;
  }
  .MapLock {
    left: 10px;
  }
}
@media screen and (min-width: 1080px) {
  .leaflet-container {
    width: 60%;
  }
  #TravelMapMainContainer {
    margin-top: -2%;
  }
}

