.StoryBoard {
  width: 100%;
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10vh;
}

#StoryBoardMainContainer {
  width: 90%;
  height: 100%;
  border: 2px goldenrod solid;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.TitleContainer {
  padding-top: 2%;
}

.OrderedList {
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.FooterContainer {
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2%;
  padding-bottom: 10%;
}

