#FooterMainContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#footerbreak {
    width: 20%;
    height: 2px;
    border-top: 3px solid #c29e29;
    margin-bottom: 1%;
}

#footerinitials {
    font-family: Abhaya Libre;
    color: white;
    letter-spacing: 1px;
    font-size: 3.5vw;
    margin-bottom: 1vh;
    
}

#footerdate {
    font-family: Abhaya Libre;
    color: white;
    font-size: 1.5vw;
    letter-spacing: 2px;
    margin-bottom: 5vh;
    margin-top: 1vh;
}

@media screen and (max-width: 768px) {
    #footerinitials {
        font-size: 7vw;
        margin-top: 5%;
    }

    #footerbreak {
        width: 20%;
        border-top-width: 2px;
    }

    #footerdate {
        font-size: 3vw;
    }
}

@media screen and (min-width: 1080px) {
   #FooterMainContainer {
    transform: scale(0.9);
    margin-top: -3%;
   }
}