#TravelMainContainer > .MenuBar > * > #travellink {
    border-bottom-color: #c29e29;
}

.ContentContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.TravelKey {
    display: flex;
    flex-direction: column;
    width: 20%;
    height: 50vh;
    margin-bottom: 6%;
    margin-left: 1%;
    border: 2px goldenrod solid;
    background-color: white;
}

.TravelMap {
    width: 100%;
    height: 100%;
    margin-left: -16%;
    margin-right: -16%;
}

.TravelList {
    display: flex;
    flex-direction: column;
    width: 35%;
    height: 50vh;
    margin-bottom: 6%;
    margin-right: 1%;
    border: 2px goldenrod solid;
    background-color: white;
}

@media screen and (max-width: 768px) {
    .ContentContainer {
        flex-direction: column;
        justify-content: flex-start;
        height: fit-content;
        margin-top: 10%;
    }

    .TravelPage {
        height: fit-content;
        width: 100%;
    }

    .TravelKey {
        width: 90%;
        height: fit-content;
        margin-left: 0;
    }

    .TravelList {
        width: 90%;
    }

    
    #TravelMainContainer > .MenuBar > * > #travellink {
        border-left: 3px solid black;
    }
}

@media screen and (min-width: 1080px) {

}